<template>
    <div class="manual">
        <nav-bar
            class="manual__nav-bar"
            title="兑换说明"
            :hairline="false"/>
        <div class="manual__content">
            <div>如果您连续30天未登录本APP，那么未提现的金币即将过期。建议您绑定手机号同时开启APP通知权限，系统会在金币清零前给您发送提醒，逾期未登录视为您自愿放弃账户的金币余额，金币将被清零。</div>
            <div>{{ manual.desc }}</div>
            <h1 class="manual__title">{{ wechatTitle }}:</h1>
            <div>
                <p
                    v-for="item in wechatItems"
                    :key="item"
                    v-html="item" />
            </div>
            <h1 class="manual__title">{{ rechargeTitle }}:</h1>
            <div>
                <p
                    v-for="item in rechargeItems"
                    :key="item"
                    v-html="item" />
            </div>
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getConfigByKey } from '@/api/common';
import { apolloRechargeDescKey } from './config';

export default {
    name: 'Manual',
    components: {
        NavBar,
    },
    data() {
        return {
            manual: {},
        };
    },
    computed: {
        wechatTitle() {
            return this.manual.wechat ? this.manual.wechat.title : '';
        },
        rechargeTitle() {
            return this.manual.recharge ? this.manual.recharge.title : '';
        },
        wechatItems() {
            return this.manual.wechat ? this.manual.wechat.items : [];
        },
        rechargeItems() {
            return this.manual.wechat ? this.manual.recharge.items : [];
        },
    },
    created() {
        this.getManual();
        this.$o2o({ e_c: '提现', e_a: '曝光', e_n: '兑换说明' });
    },
    methods: {
        getManual() {
            getConfigByKey({
                key: apolloRechargeDescKey,
            })
                .then((res) => {
                    this.manual = res.data;
                    this.$root.hidePlaceholder();
                })
                .catch(() => {
                    this.$error(this.$route.name);
                });
        },
    },
};
</script>

